<template>
    <Transition name="modal"> 
  <div class="modal" @click="$emit('showOrClose','close')">
    <div class="modal__content" @click.stop>
        <h3 class="modal__content-title"> {{id ? "Edit note" : "Add note"}} </h3>
        <form action="" class="modal__content-form" @submit.prevent="addOrChange">
            <input type="text" class="modal__content-inp" placeholder="Title" :value="title" @input="$emit('changeTitle', $event.target.value)" required >
            <textarea name="" id="" cols="30" rows="5" class="modal__content-area" :value="desc" @input="$emit('changeDesc', $event.target.value)" required>
            </textarea>
            <div class="modal__content-btns">
                <a href="#!" class="modal__content-cancel" @click="$emit('showOrClose','close')">Cancel</a>
                <button  class="modal__content-add">{{id ? "Edit " : "Add"}}</button>
            </div>
        </form>
    </div>
  </div>
</Transition>
</template>

<script>
export default {
    props: {
        title:{
            typeof: String,
            default: ''
        },
        desc:{
            typeof: String,
            default:''
        },
        id:{
            typeof: String,
            default: null
        }
    },
    methods:{
        addOrChange() {
            this.$emit("addOrChange", this.id)
            this.$emit("showOrClose", 'open')
        }
    }
}
</script>
<style lang="scss" scoped>
    .modal {
        min-height: 100vh;
        display: flex;
        align-items: center;
        justify-content: center;
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: rgba(0, 0, 0, 0.194);
        z-index: 100;
        
        &-enter-active,
        &-leavle-active {
            transition: .3s linear;
        }
        
        &-enter-from,
        &-leavle-to {
            opacity: 0;
           transform: scale(1.2 );
        }
        &-enter-to,
        &-leavle-from {
            opacity: 1;
           transform: scale(1);
        }
        
        
        
        &__content {
            max-width: 400px;
            width: 100%;
            box-shadow: var(--secondaryShadow), var(--primaryShadow);
            padding: 30px;
            border-radius: 15px;
            background: #fff;

            &-title {
                text-align: center;
                margin-bottom: 8px;
            }
            &-form {
                display: grid;
                place-items: center;
                gap: 12px;
            }
            &-inp, &-area {
                width: 100%;
                padding: 10px;
                border-radius: 5px;
                box-shadow: var(--primaryShadow);
                border: 0;
                font-size: 16px;
            }
            &-area {
                resize: vertical;
            }
            &-btns {
                display: flex;
                gap: 12px;
            }
             &-cancel {
                color: red;
             }
             &-add {
                background: transparent;
                border: 0;
                font-size: 16px;
                cursor: pointer;
                color: cornflowerblue;
             }
        }
    }
</style>