<template>
  <Header @getSearch="search = $event" />
  <Notes :notes="filterNotes" @del="delNote" @edit="editNote" />
  <Modal
    v-show="showModel"
    :title="modalTitle"
    :desc="modalDesc"
    :id="curID"
    @changeTitle="modalTitle = $event"
    @changeDesc="modalDesc = $event"
    @addOrChange="addOrChange"
    @showOrClose="closeModel"
  />

  <a href="#" class="openModel" @click.prevent="showModel = true">
    <img src="@/assets/img/edit.svg" alt="" />
  </a>
</template>

<script>
import Header from "@/components/Header.vue";
import Modal from "@/components/Modal.vue";
import Notes from "@/components/Notes.vue";
import { v4 as uuidv4 } from "uuid";

export default {
  name: "App",
  components: {
    Header,
    Modal,
    Notes,
  },
  data() {
    return {
      modalTitle: "",
      modalDesc: "",
      label: localStorage.label ? JSON.parse(localStorage.label) : [],
      showModel: false,
      curID: null,
      search: ''
    };
  },
  computed:{
    filterNotes(){
      return this.search ? this.label.filter(item => item.title.toLowerCase().includes(this.search.toLowerCase())) : this.label
    }
  },
  methods: {
    addOrChange(id) {
      let obj = {
        title: this.modalTitle,
        desc: this.modalDesc,
        date: new Date(Date.now()).toLocaleString(),
        id: id || uuidv4(),
      };
      if (id) {
        let idx = this.label.findIndex((item) => item.id == id);
        this.label[idx] = obj;
      } else {
        this.label.push(obj);
      }
      this.curID = null;

      this.closeModel("close");
      this.save();
    },
    closeModel(val) {
      if (val == "close") {
        setTimeout(() => {
          this.modalTitle = "";
          this.modalDesc = "";
          this.curID = null;
        }, 400);

        this.showModel = !this.showModel;
      }
    },
    save() {
      localStorage.label = JSON.stringify(this.label);
    },
    delNote(id) {
      let idx = this.label.findIndex((item) => item.id == id);
      this.label.splice(idx, 1);
      this.save();
    },
    editNote(id) {
      const obj = this.label.find((item) => item.id == id);
      // console.log(obj);
      this.modalTitle = obj.title;
      this.modalDesc = obj.desc;
      this.curID = obj.id;
      this.showModel = true;
    },
  },
};
</script>

<style lang="scss">
.openModel {
  position: fixed;
  bottom: 3rem;
  right: 3rem;
  width: 60px;
  height: 60px;
  display: grid;
  place-items: center;
  background: #fff;
  box-shadow: var(--secondaryShadow), var(--primaryShadow);
  border-radius: 15px;
}
</style>
